import { dynamicsObject, valueof } from '@/interfaces';
import store from '@/store';
import { COURSES_ENUM, ROLES_ENUM } from '@/utils/enums';
import { currenciesTwo, periods } from './index';

const defaultAmount = (course: valueof<COURSES_ENUM>): dynamicsObject => ({
  data: {
    amount: '',
    currency: '',
    currency_id: '',
    currency_sign: '',
  },
  inputs: [
    { show: true, id: 'amount', name: '', placeholder: ' ', type: 'text', mask: '##########', drop: false, error: false, required: true },
    { show: !!(course === COURSES_ENUM.ONE), id: 'currency', name: '', placeholder: ' ', type: 'drop', drop: true, showDrop: false, drop_data: course === COURSES_ENUM.ONE ? store?.getters?.currencyList : currenciesTwo, error: false, required: true },
  ]
});

const targetInflation = {
  data: {
    fv: ''
  },
  inputs: [
    { show: true, id: 'fv', name: '    ( )', placeholder: '    ', type: 'text', drop: false, error: false, required: false },
  ]
}

const realityReplenish = (role: keyof typeof ROLES_ENUM) => ({
  data: {
    amount: ''
  },
  inputs: [
    { show: true, id: 'amount', name: '', placeholder: role === ROLES_ENUM.STUDENT ? '  ' : ' ', type: 'number', disabled: true, drop: false, error: false, required: role === ROLES_ENUM.STUDENT }
  ]
})

const defaultExpertComment = (required = false, comment = '') => {
  return {
    data: {
      comment,
    },
    inputs: [
      { show: true, id: 'comment', name: ' ', placeholder: '  ', type: 'textarea', drop: false, error: false, required: required },
    ]
  }
}

const defaultStudentComment = {
  data: {
    student_comment: '',
  },
  inputs: [
    { show: true, id: 'student_comment', name: '  ', placeholder: '   ', type: 'textarea', drop: false, error: false, required: false },
  ]
}

const defaultBroker = (owner: keyof typeof ROLES_ENUM) => ({
  data: {
    broker: '',
  },
  inputs: [
    { show: true, id: 'broker', name: '', placeholder: '  ', type: 'text', drop: false, error: false, required: owner === ROLES_ENUM.STUDENT },
  ]
})

const conclusionPeriod = {
  data: {
    period: '',
    period_id: '',
  },
  inputs: [
    { show: true, id: 'period', name: '', placeholder: ' ', type: 'drop', drop: true, showDrop: false, drop_data: periods, error: false, required: true },
  ]
};

export { defaultAmount, defaultStudentComment, defaultExpertComment, targetInflation, conclusionPeriod, realityReplenish, defaultBroker, currenciesTwo };