import { dynamicsObject, valueof } from '@/interfaces';
import store from '@/store';
import { COURSES_ENUM, ROLES_ENUM } from '@/utils/enums';
import { classes, instrumentCountries, instrumentTypes, sectionTwo } from '../common/index';

const portfolioDistribution = {
  data: {
    core: '',
    tactic: '',
  },
  inputs: [
    { show: true, id: 'core', name: ' ', placeholder: '  ', type: 'number', drop: false, error: false, required: true },
    { show: true, id: 'tactic', name: ' ', placeholder: '  ', type: 'number', drop: false, error: false, required: true },
  ]
}

const portfolioInstrument = {
  data: {
    name: '',
    price: '',
    number_papers: '',
    currency_one: '',
    currency_two: '',
    currency_one_id: '',
    currency_two_id: '',
    instrument_type_one: '',
    instrument_type_two: '',
    instrument_type_one_id: '',
    instrument_type_two_id: '',
    lot: '',
    base_currency_one: '',
    base_currency_two: '',
    base_currency_one_id: '',
    base_currency_two_id: '',
    class_one: '',
    class_two: '',
    class_one_id: '',
    class_two_id: '',
    section_one: '',
    section_two: '',
    section_one_id: '',
    section_two_id: '',
    comment: '',
    entryPoint: '',
    exitPoint: '',
    dublicateStudent: false,
    dublicateExpert: false,
    dublicateExisting: false,
  }
}

const twoInputs = [
  { show: true, id: 'entryPoint', name: ' ', placeholder: '  ', type: 'text', drop: false, error: false, required: false },
  { show: true, id: 'exitPoint', name: ' / ', placeholder: '  / ', type: 'text', drop: false, error: false, required: false },
]

const existingPortfolioInstrument = (course: valueof<COURSES_ENUM>, type: string, role: keyof typeof ROLES_ENUM, owner?: keyof typeof ROLES_ENUM): dynamicsObject => {
  const comment = role === ROLES_ENUM.STUDENT ? [
    { show: true, id: 'commentStudent', name: '  ', placeholder: '  ', type: 'textarea', drop: false, error: false, required: false },
  ] : [
    { show: true, id: 'comment', name: ' ', placeholder: '  ', type: 'textarea', drop: false, error: false, required: false },
  ];
  const formula = course === COURSES_ENUM.ONE ?
    { show: true, id: 'formula', name: '  ', placeholder: '  ', type: 'number', drop: false, formula: 'purchase_price * number_papers', error: false, required: true } :
    { show: true, id: 'formula', name: '   *  ', placeholder: '  ', type: 'number', drop: false, formula: 'price * number_papers', error: false, required: true }
  const additional = (type === 'tactic' && course === COURSES_ENUM.TWO ? twoInputs : []);
  const lot = course === COURSES_ENUM.ONE ? [] : [
    { show: true, id: 'lot', name: '', placeholder: ' ', formula_element: true, type: 'number', drop: false, error: false, required: true }
  ];
  return {
    data: {
      purchase_price: '',
      formula: '',
      ...portfolioInstrument.data,
    },
    inputs: [
      { show: true, id: 'name', name: '', placeholder: ' ', type: 'text', drop: true, error: false, required: true, uncheck: true },
      { show: true, id: 'purchase_price', name: ' ', placeholder: '  ', formula_element: true, type: 'number', drop: false, error: false, required: !(role === ROLES_ENUM.EXPERT && owner === ROLES_ENUM.STUDENT) },
      { show: true, id: 'price', name: ' ', placeholder: '  ', formula_element: true, type: 'number', drop: false, error: false, required: true },
      { show: true, id: 'number_papers', name: ' ', placeholder: '  ', formula_element: true, type: 'number', drop: false, error: false, required: true },
      ...lot,
      formula,
      { show: true, id: 'currency_' + course, name: '  ', placeholder: '   ', type: 'drop', drop: true, showDrop: false, drop_data: store?.getters?.currencyList, error: false, required: true },
      { show: true, id: 'base_currency_' + course, name: '     ', placeholder: '     ', type: 'drop', drop: true, showDrop: false, drop_data: store?.getters?.currencyList, error: false, required: true },
      { show: true, id: 'instrument_type_' + course, name: ' ', placeholder: '  ', type: 'drop', drop: true, showDrop: false, drop_data: instrumentTypes(course), error: false, required: true, uncheck: true },
      { show: true, id: 'class_' + course, name: ' ', placeholder: '  ', type: 'drop', drop: true, showDrop: false, drop_data: classes(course), error: false, required: true },
      { show: true, id: 'country_' + course, name: '', placeholder: ' ', type: 'drop', drop: true, showDrop: false, drop_data: instrumentCountries, error: false, required: true },
      { show: true, id: 'section_' + course, name: ' ', placeholder: '  ', type: 'text', drop: !!(course === COURSES_ENUM.TWO), showDrop: false, drop_data: course === COURSES_ENUM.ONE ? [] : sectionTwo, error: false, required: false, uncheck: true },
      ...additional,
      ...comment,
    ]
  }
};

const studentPortfolioInstrument = (course: valueof<COURSES_ENUM>, type: string, role: keyof typeof ROLES_ENUM, owner?: keyof typeof ROLES_ENUM): dynamicsObject => {
  const comment = role === ROLES_ENUM.STUDENT ? [
    { show: true, id: 'commentStudent', name: '  ', placeholder: '  ', type: 'textarea', drop: false, error: false, required: false },
  ] : [
    { show: true, id: 'comment', name: ' ', placeholder: '  ', type: 'textarea', drop: false, error: false, required: false },
  ];
  const additional = (type === 'tactic' && course === COURSES_ENUM.TWO ? twoInputs : []);
  const lot = course === COURSES_ENUM.ONE ? [] : [
    { show: true, id: 'lot', name: '', placeholder: ' ', formula_element: true, type: 'number', drop: false, error: false, required: true }
  ];
  return {
    data: {
      percent: '',
      ...portfolioInstrument.data,
    },
    inputs: [
      { show: true, id: 'name', name: '', placeholder: ' ', type: 'text', drop: true, error: false, required: true, uncheck: true },
      { show: true, id: 'price', name: ' ', placeholder: '  ', formula_element: true, type: 'number', drop: false, error: false, required: true },
      { show: true, id: 'number_papers', name: ' ', placeholder: '  ', formula_element: true, type: 'number', drop: false, error: false, required: true },
      ...lot,
      { show: true, id: 'formula', name: '  ', placeholder: '  ', type: 'number', drop: false, formula: 'price * number_papers', error: false, required: true },
      { show: true, id: 'currency_' + course, name: '  ', placeholder: '   ', type: 'drop', drop: true, showDrop: false, drop_data: store?.getters?.currencyList, error: false, required: true },
      { show: true, id: 'base_currency_' + course, name: '     ', placeholder: '     ', type: 'drop', drop: true, showDrop: false, drop_data: store?.getters?.currencyList, error: false, required: true },
      { show: true, id: 'instrument_type_' + course, name: ' ', placeholder: '  ', type: 'drop', drop: true, showDrop: false, drop_data: instrumentTypes(course), error: false, required: true, uncheck: true },
      { show: true, id: 'class_' + course, name: ' ', placeholder: '  ', type: 'drop', drop: true, showDrop: false, drop_data: classes(course), error: false, required: true },
      { show: true, id: 'country_' + course, name: '', placeholder: ' ', type: 'drop', drop: true, showDrop: false, drop_data: instrumentCountries, error: false, required: true },
      { show: true, id: 'section_' + course, name: ' ', placeholder: '  ', type: 'text', drop: !!(course === 'two'), showDrop: false, drop_data: course === COURSES_ENUM.ONE ? [] : sectionTwo, error: false, required: false, uncheck: true },
      ...additional,
      ...comment,
    ]
  }
};

