import { defaultAmount, targetInflation } from '../common/defaults';
import { portfolios, duration } from '../common/index';
import copyObject from '@/utils/copyObject';
import { COURSES_ENUM, ROLES_ENUM } from '@/utils/enums';
import { valueof } from '@/interfaces';

const termField = {
  data: {
    term: '',
    duration: '',
    duration_id: '',
  },
  inputs: [
    { show: true, id: 'term', name: '', placeholder: ' ', type: 'text', drop: false, mask: '###', error: false, required: true },
    { show: true, id: 'duration', name: '/', placeholder: ' /', type: 'drop', drop: true, showDrop: false, drop_data: duration, error: false, required: true },
  ]
};
const riskPortfolioField = {
  data: {
    portfolio: '',
    portfolio_id: '',
  },
  inputs: [
    { show: true, id: 'portfolio', name: '-', placeholder: ' -', type: 'drop', drop: true, showDrop: false, drop_data: portfolios, error: false, required: true },
  ]
};

const firstType = {
  id: 1,
  name: '   ',
  section_name: ' '
};

const secondType = {
  id: 2,
  name: ' ',
  section_name: '     (  )'
}

const names = {
  student: {
    STUDENT: '   ',
    EXPERT: '   ,  ',
    OWNER: '   ,  ',
    SUPPORT: '',
  },
  resourses: {
    STUDENT: '  (     +       )',
    EXPERT: ' ',
    OWNER: ' ',
    SUPPORT: '',
  }
}

const defaultType = (course: valueof<COURSES_ENUM>, role: keyof typeof ROLES_ENUM) => {
  return {
    id: 2,
    name: ' ',
    sections: [
      { show: true, name: '     (  )', modules: [ copyObject(defaultAmount(course)) ], adding: false, default: 'income-field' },
      { show: true, name: '  ', modules: [ copyObject(termField) ], adding: false, default: 'term-field' },
      { show: true, name: names.student[role], modules: [ copyObject(defaultAmount(course)) ], adding: false, default: 'student-field' },
      { show: !!(role === ROLES_ENUM.EXPERT), name: '      FV', modules: [ copyObject(targetInflation) ], adding: false, default: 'targetInflation-field' },
      { show: true, name: names.resourses[role], modules: [ copyObject(defaultAmount(course)) ], adding: !!(course === COURSES_ENUM.ONE), default: 'resources-field' },
      { show: true, name: '- ', modules: [ copyObject(riskPortfolioField) ], adding: false, default: 'portfolio-field' },
    ],
  }
};

export { defaultType, firstType, secondType, termField, riskPortfolioField };